import React, { usEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { useStaticQuery, graphql } from 'gatsby';
import { Container as GridContainer, Row, Col } from 'react-grid-system';
import { Parallax } from 'react-parallax';

import Layout from '../components/layout';
import SEO from '../components/seo';
import theme from '../utils/themeconstants';
import Section from '../components/section';
import Offerte from '../components/sections/offerte';
import ListeningSamples from '../components/sections/listening-samples';
import Arrow from '../components/arrow';

export default () => {
  const { background1 } = useStaticQuery(
    graphql`
      query {
        background1: file(relativePath: { eq: "background1.jpg" }) {
          childImageSharp {
            fluid(maxWidth: 1920, quality: 80) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }
      }
    `,
  );
  return (
    <Layout dark>
      <SEO
        title="Skyfour - Hochzeitsband und Partyband - Buchen Sie Partymusik, Hochzeitsmusik und Live Jazz"
        description="Live-Band und Partyband - Wir liefern die perfekte Partymusik, ob als Hochzeitsband, Event-Band oder Jazzband."
      />
      <StyledBackgroundImage
        strength={200}
        bgImage={background1.childImageSharp.fluid.src}
        bgImageStyle={{ objectFit: 'cover' }}
      >
        <GridContainer>
          <Row>
            <Col md={6}>
              <StageTitle>
                <GridContainer>
                  <h1>Musik fr unvergessliche Momente</h1>
                  <p style={{ marginBottom: '2.2rem' }}>
                    Ihre Live Band fr den perfekten Sound. Ob intimer Jazz oder
                    energetische Partymusik, wir sorgen fr die richtige
                    Stimmung.
                  </p>
                </GridContainer>
              </StageTitle>
            </Col>
          </Row>
        </GridContainer>
        <Arrow />
      </StyledBackgroundImage>
      <Section>
        <GridContainer>
          <Row>
            <h2
              style={{
                width: '100%',
                margin: '0 auto 1.5em auto',
                textAlign: 'center',
              }}
            >
              Skyfour, Ihre Live Partyband
            </h2>
            <Col md={6}>
              <p style={{ marginBottom: '2em' }}>
                Skyfour spielt von live Jazz ber Pop und Rnb, bis zur aktuellen
                Hitparade, alles was Sie sich wnschen. Suchen Sie ein Duo mit
                Gesang und Piano fr ihre Hochzeit, eine dezente Jazzband fr
                ihren Event, oder die volle Partymusik mit der ganzen Live Band
                fr ihre Party? Wir helfen ihnen gerne, fr ihren Anlass die
                richtige Formation zu finden!
              </p>
            </Col>
            <Col md={6}>
              <p>
                Die Profimusiker der Live Band Skyfour sind sowohl bezglich
                Formation und Stil usserst flexibel und verleihen ihrem Event
                das richtige Ambiente. Ob fr Hochzeiten, Geburtstags-,
                Weihnachtsfeiern oder Firmenevents, profitieren Sie von unserer
                Erfahrung und lassen Sie sich unverbindlich beraten.
              </p>
            </Col>
          </Row>
          <div style={{ maxWidth: 600, margin: '2em auto 0' }}>
            <PlayerWrapper>
              <iframe
                title="skyfour video"
                width="100%"
                height="100%"
                style={{ position: 'absolute', top: 0, left: 0 }}
                src="https://www.youtube.com/embed/QgZj0oc_NBQ"
                srcDoc="<style>*{padding:0;margin:0;overflow:hidden}html,body{height:100%}img,span{position:absolute;width:100%;top:0;bottom:0;margin:auto}span{height:1.5em;text-align:center;font:48px/1.5 sans-serif;color:white;text-shadow:0 0 0.5em black}</style><a href=https://www.youtube.com/embed/QgZj0oc_NBQ?autoplay=1><img src=https://img.youtube.com/vi/QgZj0oc_NBQ/hqdefault.jpg alt='Skyfour'><span></span></a>"
                frameBorder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowFullscreen
              />
            </PlayerWrapper>
          </div>
        </GridContainer>
      </Section>
      <ListeningSamples />
      <Offerte />
    </Layout>
  );
};

const appear = keyframes`
  from {
    transform: translateY(50px);
    opacity: 0;
  }

  to {
    transform: translateY(0px);
    opacity: 1;
  }
`;

const appearScale = keyframes`
  from {
    opacity: 0;
    transform: scale(1.1);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
`;

const StyledBackgroundImage = styled(Parallax)`
  height: 100vh;
  overflow: hidden;
  animation: ${appearScale} 0.8s ease;
  animation-delay: 0;
  opacity: 0;
  animation-fill-mode: forwards;
  @media only screen and (max-width: ${theme.dim.mobilebreakpoint}px) {
    height: auto;
  }
`;

const StageTitle = styled.div`
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  animation: ${appear} 0.8s ease;
  animation-delay: 0.2s;
  opacity: 0;
  animation-fill-mode: forwards;
  h1 {
    font-size: 42px;
    color: white;
  }
  p {
    line-height: 1.2;
    color: white;
  }
  h3 {
    font-weight: lighter;
    font-size: 18px;
    margin-bottom: 8px;
    color: white;
  }
`;

const PlayerWrapper = styled.div`
  position: relative;
  padding-top: 56.25%; /* 720 / 1280 = 0.5625 */
`;
